import React, { useState, useEffect } from "react";

import { Line, SliderSwiper, SmartCircle, SmartYears } from "../";

import styles from "./HistoricalDates.module.scss";

export type DateT = {
  year: string;
  description: string;
};

export type CategoryT = {
  name: string;
  datesList: DateT[];
};

export const HistoricalDates: React.FC = () => {
  const categories: CategoryT[] = [
    {
      name: "",
      datesList: [
        {
          year: "1969",
          description:
            "20       :        ,    .",
        },
        {
          year: "1928",
          description:
            "12      :  ,   .",
        },
        {
          year: "1953",
          description:
            "25          .",
        },
        {
          year: "1986",
          description:
            "28     Challenger    .",
        },
        {
          year: "1990",
          description: "24      Hubble.",
        },
      ],
    },
    {
      name: "",
      datesList: [
        {
          year: "1939",
          description: '12     " ".',
        },
        {
          year: "1977",
          description:
            '25     " :  IV   ".',
        },
        {
          year: "1994",
          description: '7     " ".',
        },
        {
          year: "1982",
          description: '25     "".',
        },
        {
          year: "2009",
          description: '18     "".',
        },
      ],
    },
    {
      name: "",
      datesList: [
        {
          year: "1949",
          description: '17     "1984"  .',
        },
        {
          year: "1865",
          description:
            "14       ,   ,    .",
        },
        {
          year: "1816",
          description:
            '16      ""  .',
        },
        {
          year: "1605",
          description: '1605    " "   .',
        },
        {
          year: "1850",
          description:
            '30     " "  .',
        },
      ],
    },
    {
      name: "",
      datesList: [
        {
          year: "1066",
          description:
            "14     ,     .",
        },
        {
          year: "1912",
          description: "15    .",
        },
        {
          year: "1789",
          description:
            "14        .",
        },
        {
          year: "1945",
          description: "6     .",
        },
        {
          year: "1963",
          description:
            '28      - "".',
        },
      ],
    },
    {
      name: "",
      datesList: [
        {
          year: "1936",
          description: "1        .",
        },
        {
          year: "1954",
          description:
            "6       ,    4 .",
        },
        {
          year: "1992",
          description: "25        .",
        },
        {
          year: "1972",
          description:
            "5    :            .",
        },
        {
          year: "2008",
          description: "8        .",
        },
      ],
    },
  ];
  const [curCategory, setCurCategory] = useState(0);
  const [matches, setMatches] = useState(
    window.matchMedia("(max-width: 768px)").matches
  );

  useEffect(() => {
    window.matchMedia("(max-width: 768px)").addEventListener("change", (e) => {
      setMatches(e.matches);
    });
  }, []);

  return (
    <div className={styles.root}>
      <article className={styles.root__title}>
        <h2 className={styles.root__title_content}> </h2>
      </article>
      <SmartCircle
        categoriesList={categories}
        activeDot={curCategory}
        setActiveDot={setCurCategory}
      />
      <SmartYears
        yearsList={categories[curCategory].datesList.map((date) =>
          Number(date.year)
        )}
      />
      <SliderSwiper datesList={categories[curCategory].datesList} media={matches} />
      {!matches && (
        <>
          <Line
            isVertical={true}
            styles={{
              position: "absolute",
              left: "0",
              top: "0",
            }}
          />
          <Line
            isVertical={true}
            styles={{
              position: "absolute",
              right: "0",
              top: "0",
            }}
          />
          <Line
            isVertical={true}
            styles={{
              position: "absolute",
              right: "50%",
              top: "0",
            }}
          />
        </>
      )}
      <Line
        isVertical={false}
        styles={{
          position: "absolute",
          left: "0",
          top: `${
            !matches
              ? "calc((1vh + 1vw) * 16.000001)"
              : "calc((1vw + 1vh) * 32.995495)"
          }`,
        }}
      />
    </div>
  );
};
